import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { FaCopy, FaCheck } from 'react-icons/fa';
import { useTheme } from '../context/ThemeContext';

function CodeSnippet({
  code,
  title = 'Code Example',
  animated = false,
  animationSpeed = 30,
  showCopyButton = true,
}) {
  const { theme } = useTheme();
  const [displayedCode, setDisplayedCode] = useState(animated ? '' : code);
  const [copied, setCopied] = useState(false);
  const [animationComplete, setAnimationComplete] = useState(!animated);

  useEffect(() => {
    if (!animated) return;

    let index = 0;
    const timer = setInterval(() => {
      if (index <= code.length) {
        setDisplayedCode(code.slice(0, index));
        index++;
      } else {
        setAnimationComplete(true);
        clearInterval(timer);
      }
    }, animationSpeed);

    // eslint-disable-next-line consistent-return
    return () => clearInterval(timer);
  }, [code, animated, animationSpeed]);

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(code);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (err) {
      console.error('Failed to copy code:', err);
    }
  };

  const containerStyle = {
    position: 'relative',
    background: '#2d2d2d',
    border: '1px solid #555',
    borderRadius: '12px',
    overflow: 'hidden',
    boxShadow: '0 4px 8px rgba(0, 0, 0, 0.3)',
    margin: '1.5rem 0',
  };

  const headerStyle = {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    padding: '0.75rem 1rem',
    background: '#1a1a1a',
    borderBottom: '1px solid #555',
  };

  const titleStyle = {
    color: '#ffffff',
    fontFamily: 'Courier New, monospace',
    fontSize: '0.875rem',
    fontWeight: 'bold',
  };

  const buttonStyle = {
    background: 'none',
    border: '1px solid #f7b733',
    color: '#f7b733',
    padding: '0.25rem 0.5rem',
    borderRadius: '6px',
    cursor: 'pointer',
    fontSize: '0.75rem',
    display: 'flex',
    alignItems: 'center',
    gap: '0.25rem',
  };

  const codeStyle = {
    margin: 0,
    padding: '1rem',
    fontFamily: 'Courier New, monospace',
    fontSize: '0.875rem',
    lineHeight: '1.5',
    overflowX: 'auto',
    whiteSpace: 'pre-wrap',
    wordWrap: 'break-word',
    backgroundColor: '#1a1a1a',
    color: '#ffffff',
    border: 'none',
  };

  return (
    <div style={containerStyle}>
      <div style={headerStyle}>
        <div style={titleStyle}>{title}</div>
        {showCopyButton && (
          <button style={buttonStyle} onClick={handleCopy}>
            {typeof window !== 'undefined' && (copied ? <FaCheck /> : <FaCopy />)}
            {copied ? 'Copied!' : 'Copy'}
          </button>
        )}
      </div>
      <pre style={codeStyle}>
        {displayedCode}
      </pre>
    </div>
  );
}

CodeSnippet.propTypes = {
  code: PropTypes.string.isRequired,
  // eslint-disable-next-line react/require-default-props
  title: PropTypes.string,
  animated: PropTypes.bool,
  // eslint-disable-next-line react/require-default-props
  animationSpeed: PropTypes.number,
  showCopyButton: PropTypes.bool,
};

export default CodeSnippet;
